//condition paramscalulator
export const DMG_STEP_4 = {
  HAMMER_HELM: { dmg_type: "PHYSICAL", dmg: 1 },
  DEVASTATING: { dmg_type: "PHYSICAL", dmg: 2 },
  TRICK_SHOT: { dmg_type: "PHYSICAL", dmg: 3 },
  CQC: { dmg_type: "PHYSICAL", dmg: 1, multiplier: "USER_INPUT" },
  BACKSTAB: {
    condition: {
      params: ["fieldStatus"],
      run: (fieldStatus) => {
        return fieldStatus?.includes["FLANKING"];
      },
    },
    dmg_type: "PHYSICAL",
    dmg: 2,
  },
  PRECISE_STRIKE: {
    condition: {
      params: ["attackRoll", "enemyDefense"],
      run: (roll, def) => {
        return roll - def >= 4;
      },
    },
    dmg_type: "IRRERUCIBLE",
    dmg: 5,
  },

  WILT: {
    condition: {
      //wiltuiwilt
      //conditionfalse
      params: ["enemy", "physicalDamage", "magicDamage", "irreducibleDamage"],
      run: (enemy, phyDmg, magDmg, irrDmg) => {
        if (enemy.resistance.length > 0) {
          return false;
        }
        return phyDmg > 0 || magDmg > 0 || irrDmg > 0;
      },
    },
    dmg_type: "IRRERUCIBLE",
    dmg: 2,
  },
  NEMESIS: {
    condition: {
      params: ["physicalDamage", "magicDamage", "irreducibleDamage"],
      run: (pd, md, hd) => {
        let pool = [];
        if (pd > 0) {
          pool.push("PHYSICAL");
        }
        if (md > 0) {
          pool.push("MAGIC");
        }
        if (hd > 0) {
          pool.push("IRREDUCIBLE");
        }
        return {
          available: pool,
        };
      },
    },
    dmg_type: "OPTIMAL",
    dmg: 2,
  },
  //vow token
  VOW: {
    condition: {
      params: ["physicalDamage", "magicDamage", "irreducibleDamage"],
      run: (pd, md, hd) => {
        let pool = [];
        if (pd > 0) {
          pool.push("PHYSICAL");
        }
        if (md > 0) {
          pool.push("MAGIC");
        }
        if (hd > 0) {
          pool.push("IRREDUCIBLE");
        }
        return {
          available: pool,
        };
      },
    },
    dmg_type: "OPTIMAL",
    multiplier: "USER_INPUT",
    dmg: 2,
  },
};
